
// 暗色调主题色
$primary-color: #6b8afd;
$danger-color: #ff6b6b;
$success-color: #51cf66;
$warning-color: #ffd43b;
$bg-dark: #0e0e0e;
$bg-medium: #1a1a1a;
$bg-light: #252525;
$bg-hover: #2a2a2a;
$text-primary: #e4e4e4;
$text-secondary: #888888;
$border-color: #2a2a2a;
$border-light: #333333;

.status-bar-container {
  width: 100%;
  max-height: 760px;
  background: $bg-dark;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.6);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  font-size: 11px;
  display: flex;
  flex-direction: column;
  border: 1px solid $border-color;
}

.tabs {
  display: flex;
  background: $bg-medium;
  flex-shrink: 0;
  border-bottom: 1px solid $border-color;

  .tab-button {
    flex: 1;
    padding: 6px 4px;
    text-align: center;
    cursor: pointer;
    font-size: 11px;
    font-weight: 500;
    color: $text-secondary;
    transition: all 0.15s ease;
    border-bottom: 2px solid transparent;
    white-space: nowrap;
    position: relative;

    &:hover {
      background: $bg-hover;
      color: $text-primary;
    }

    &.active {
      color: $primary-color;
      background: $bg-dark;
      
      &::after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        right: 0;
        height: 2px;
        background: $primary-color;
        box-shadow: 0 0 8px rgba($primary-color, 0.5);
      }
    }
  }
}

.tab-content {
  flex: 1;
  padding: 6px;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #444 transparent;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: #444;
    border-radius: 2px;
    
    &:hover {
      background: #555;
    }
  }
}

.empty-state {
  text-align: center;
  color: #555;
  padding: 16px 8px;
  font-size: 11px;
}

// 通用组件
.section {
  margin-bottom: 6px;
  padding: 6px;
  background: $bg-light;
  border-radius: 3px;
  border: 1px solid $border-color;
}

.section-header {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 11px;
  margin-bottom: 4px;
  flex-wrap: wrap;

  .protagonist-name {
    font-weight: 600;
    color: $text-primary;
  }

  .protagonist-age {
    color: $text-secondary;
    font-size: 10px;
  }

  .protagonist-location {
    margin-left: auto;
    font-size: 10px;
    color: $primary-color;
  }
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4px;

  @media (max-width: 320px) {
    grid-template-columns: 1fr;
  }
}

.info-cell {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 3px 4px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 2px;

  .label {
    font-size: 10px;
    flex-shrink: 0;
    opacity: 0.8;
  }

  .value {
    font-size: 10px;
    color: $text-primary;
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2px 0;
  font-size: 10px;

  .label {
    color: $text-secondary;
  }

  .value {
    color: $text-primary;
    font-weight: 500;
  }
}

// 折叠面板
.collapsible {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 6px;
  background: $bg-light;
  border: 1px solid $border-color;
  border-radius: 3px;
  cursor: pointer;
  font-size: 10px;
  font-weight: 500;
  color: $text-primary;
  margin-bottom: 2px;
  transition: all 0.15s ease;
  user-select: none;

  &:hover {
    background: $bg-hover;
    border-color: $border-light;
  }

  .chevron {
    font-size: 8px;
    color: $text-secondary;
    transition: transform 0.15s ease;

    &.open {
      transform: rotate(90deg);
    }
  }
}

// 物品网格
.items-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 3px;
  padding: 4px;
  background: $bg-medium;
  border: 1px solid $border-color;
  border-top: none;
  border-radius: 0 0 3px 3px;
  margin-bottom: 6px;
}

.item-chip {
  display: inline-flex;
  align-items: center;
  gap: 2px;
  padding: 2px 5px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  font-size: 9px;
  transition: all 0.15s ease;

  &:hover {
    background: rgba(255, 255, 255, 0.08);
    border-color: rgba(255, 255, 255, 0.15);
  }

  .item-name {
    color: $text-primary;
  }

  .item-qty {
    color: $primary-color;
    font-weight: 600;
  }

  .item-durability {
    padding: 0 2px;
    border-radius: 2px;
    font-size: 8px;

    &.high { color: $success-color; }
    &.medium { color: $warning-color; }
    &.low { color: $danger-color; }
  }
}

// 角色面板
.characters-panel {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.char-card {
  background: $bg-light;
  border: 1px solid $border-color;
  border-radius: 3px;
  overflow: hidden;

  .char-header {
    display: flex;
    align-items: center;
    padding: 5px 6px;
    cursor: pointer;
    gap: 6px;
    transition: background 0.15s ease;

    &:hover {
      background: $bg-hover;
    }

    .char-basic {
      display: flex;
      align-items: center;
      gap: 4px;
      flex: 1;
      min-width: 0;

      .char-name {
        font-size: 11px;
        font-weight: 600;
        color: $text-primary;
        white-space: nowrap;
      }

      .char-relation {
        font-size: 9px;
        color: $text-secondary;
        background: rgba(255, 255, 255, 0.05);
        padding: 1px 4px;
        border-radius: 2px;
        white-space: nowrap;
      }
    }

    .char-status-tags {
      .status-tag {
        font-size: 8px;
        padding: 1px 4px;
        border-radius: 2px;
        white-space: nowrap;

        &.safe { background: rgba($success-color, 0.2); color: $success-color; }
        &.danger { background: rgba($danger-color, 0.2); color: $danger-color; }
        &.ovulation { background: rgba(#ff4081, 0.2); color: #ff4081; }
        &.near-ovulation { background: rgba($warning-color, 0.2); color: $warning-color; }
        &.period { background: rgba(#9c27b0, 0.2); color: #9c27b0; }
        &.unknown { background: rgba(255, 255, 255, 0.05); color: $text-secondary; }
      }
    }

    .chevron {
      font-size: 8px;
      color: $text-secondary;
      transition: transform 0.15s ease;

      &.open {
        transform: rotate(90deg);
      }
    }
  }

  .char-details {
    padding: 6px;
    background: $bg-medium;
    border-top: 1px solid $border-color;
    animation: slideDown 0.15s ease-out;
  }
}

.stats-row {
  display: flex;
  gap: 8px;
  margin-bottom: 6px;

  .stat-item {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 4px;

    .stat-label {
      font-size: 9px;
      color: $text-secondary;
      width: 24px;
    }

    .stat-bar-sm {
      flex: 1;
      height: 4px;
      background: rgba(255, 255, 255, 0.08);
      border-radius: 2px;
      overflow: hidden;

      .stat-fill {
        height: 100%;
        border-radius: 2px;
        transition: width 0.2s ease;

        &.affection {
          background: linear-gradient(90deg, #ff4081, #f50057);
          box-shadow: 0 0 8px rgba(#ff4081, 0.4);
        }

        &.lust {
          background: linear-gradient(90deg, $danger-color, darken($danger-color, 5%));
          box-shadow: 0 0 8px rgba($danger-color, 0.4);
        }
      }
    }

    .stat-val {
      font-size: 9px;
      font-weight: 600;
      color: $text-primary;
      width: 18px;
      text-align: right;
    }
  }
}

.char-info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2px 6px;
  font-size: 9px;
  margin-bottom: 4px;

  .char-info-item {
    display: flex;
    justify-content: space-between;
    padding: 1px 0;

    &.full-width {
      grid-column: 1 / -1;
    }

    .label {
      color: $text-secondary;
    }

    .value {
      color: $text-primary;
      font-weight: 500;
      text-align: right;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      max-width: 60%;
    }
  }
}

.outfit-section, .items-section {
  margin-top: 4px;
  padding-top: 4px;
  border-top: 1px solid rgba(255, 255, 255, 0.08);

  .mini-label {
    font-size: 8px;
    color: $text-secondary;
    text-transform: uppercase;
    letter-spacing: 0.3px;
    display: block;
    margin-bottom: 2px;
  }
}

.outfit-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 2px;

  .outfit-tag {
    font-size: 8px;
    padding: 1px 4px;
    background: rgba($primary-color, 0.2);
    color: $primary-color;
    border-radius: 2px;
  }
}

.char-items {
  display: flex;
  flex-wrap: wrap;
  gap: 3px;

  .char-item {
    font-size: 8px;
    padding: 1px 4px;
    background: rgba($warning-color, 0.2);
    color: $warning-color;
    border-radius: 2px;

    .item-qty {
      font-weight: 600;
    }
  }
}

// 日志面板
.log-panel {
  .log-entry {
    padding: 4px 6px;
    background: $bg-light;
    border: 1px solid $border-color;
    border-radius: 2px;
    margin-bottom: 3px;
    transition: all 0.15s ease;

    &:hover {
      background: $bg-hover;
      border-color: $border-light;
    }

    .log-time {
      font-size: 9px;
      color: $text-secondary;
      margin-right: 6px;
    }

    .log-desc {
      font-size: 10px;
      color: $text-primary;
    }

    .log-chars {
      display: flex;
      flex-wrap: wrap;
      gap: 2px;
      margin-top: 2px;

      .log-char-tag {
        font-size: 8px;
        padding: 1px 4px;
        background: rgba($primary-color, 0.2);
        color: $primary-color;
        border-radius: 2px;
      }
    }
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    max-height: 0;
  }
  to {
    opacity: 1;
    max-height: 500px;
  }
}

// 移动端适配
@media (max-width: 360px) {
  .status-bar-container {
    font-size: 10px;
  }

  .tabs .tab-button {
    padding: 5px 2px;
    font-size: 10px;
  }

  .tab-content {
    padding: 4px;
  }

  .char-basic .char-name {
    font-size: 10px;
  }

  .char-info-grid {
    grid-template-columns: 1fr;
  }
}
